<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle"></p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle"></p>
            <p>
                <span class="code">vertical-align</span>  <span class="code">middle</span> 
            </p>
            <p>
                 <span class="code">div</span> <span class="code">vertical-align</span>  <span class="code">margin-bottom</span>  <span class="code">div</span>
            </p>
            <div class="test">
                <div class="v">vertical-align</div>
            </div>
            <div class="test">
                <div class="m">margin-bottom</div>
            </div>
            <div style="clear:both"></div>
            <p>
                <span class="code">vertical-align</span>  <span class="code">margin</span> 
            </p>
            <p>
                 <span class="code">display:table</span> <span class="code">display:table-cell;vertical-align:middle;</span>
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>
<style>
    .test:first-of-type{
        display:table;
    }
    .test{
        float:left;
        width:120px;
        line-height:120px;
        height:120px;
        margin-left:40px;
        overflow: hidden;
        -webkit-box-shadow:0 0 10px 0 #ddd;
        -moz-box-shadow:0 0 10px 0 #ddd;
        box-shadow:0 0 10px 0 #ddd;
    }
    .test .v{
        display: inline-block;
        vertical-align:50px;
    }
    .test .m{
        display: inline-block;
        margin-bottom:50px;
    }
</style>


