            </p>
            <p>
                2 <span class="tip">dist</span>  <span class="tip">static</span>  <span class="tip">index.html</span> 
            </p>
            <p>
                3
            </p>
            <p>
                 <span class="tip">build</span>  <span class="tip">js</span>  <span class="tip">css</span>  <span class="tip">404</span> <span class="tip">webpack</span> <span class="tip">config/index.js</span>  <span class="code">build.assetsPublicPath</span> <span class="code">''</span>
             </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


