<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle"></p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle"></p>
            <p>
               
            </p>
            <pre class="code"><span class="key">.parent div</span><span class="flower">{</span><span class="fun">height</span><span class="word">:</span><span class="str">100%</span><span class="flower">}</span><br><span class="key">.parent</span><span class="flower">{</span><span class="fun">height</span><span class="word">:</span><span class="str">200px;</span><span class="flower">}</span></pre>
           <p>
               <span class="code">html</span>  <span class="code">auto</span> <span class="code">0</span>
           </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


