<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle"></p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle"></p>
            <p>
                1
            </p>
            <p>
                <span class="code">width</span>         
            </p>
            <pre class="code"><span class="tag">img</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">width</span><span class="word">:</span><span class="str">100%</span><span class="word">;</span><span class="com">// 100px</span><br><span class="flower">}</span></pre>
            <p>
                2
            </p>
            <p>
                <span class="code">height</span>  <span class="code">height </span>
            </p>
            <pre class="code"><span class="tag">img</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">height</span><span class="word">:</span><span class="str">100px</span><span class="word">;</span><span class="com">// 100%    </span><br><span class="flower">}</span></pre>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


