<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle"></p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle"></p>
            <p>
                 <span class="tip">/#/</span> 
            </p>
            <pre class="code"><span class="key">const </span><span class="param">router</span><span class="key"> = new </span><span class="word">VueRouter(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">mode</span><span class="word">:</span><span class="str">'history'</span><span class="word">,</span><span class="com"> //  ok </span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">routes</span><span class="word">:</span><span class="param">routeConfig</span><br><span class="flower">}</span></pre>
            <p>
                 <span class="tip">history</span>  <span class="tip">/#/</span> <span class="tip">js</span> 
            </p>
            <p>
                 <span class="tip">/#/</span>  <span class="tip">history</span>  <span class="tip">url</span> 
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


