<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle"></p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle"></p>
            <p class="minititle"></p>
            <p>
                 <span class="tip">jQuery</span> 
            </p>
            <p>
                
            </p>
            <p class="minititle"></p>
            <p>
                
            </p>
            <p>
                <span class="tip">$('#obj').offset().top + $('#obj').height() &lt; $(window).height() + $(document).scrollTop()</span>
            </p>
            <p>
                 <span class="tip">true</span> <span class="tip">$(window).height()</span> 
            </p>
            <p>
                 <span class="tip">$(window).height()</span>  <span class="tip">$(document).height()</span> 
            </p>
            <p class="minititle"></p>
            <p>
                 <span class="tip">HTML</span> 
            </p>
            <p>
                 <span class="tip">&lt;!DOCTYPE html&gt;</span> <span class="tip">H5</span>
            </p>
            <p>
                
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


