//  10  
Array.prototype.copyIndex = function(item){
    let current = [];
    this.forEach( (e,i) => {
        if( i <= item - 1 ) current.push(e);
        else return false;
    });
    return current;
};

//  
Array.prototype.copyAll = function(){
    let current = [];
    this.forEach( (e,i) => {
        current.push(e);
    });
    return current;
};

//  
const searchDemoData  = initialDemoData.copyAll();
const searchBlogData  = initialBlogData.copyAll();
const currentDemoData = initialDemoData.copyIndex(10);
const currentBlogData = initialBlogData.copyIndex(10);
const formatSearch = (key) => {
    return key.toLowerCase().replace(/\s/g,'');
};
// vuex 
const store = new Vuex.Store({
    state:{
        owner:baseInfo.owner,
        motto:baseInfo.motto,
        pageSize        : 10,
        btnOpacity      : 0,
        menuOpacity     : 1,
        isScroll        : false,
        tags            : allTags,
        currentDemoData : currentDemoData,
        searchDemoData  : searchDemoData,
        initialDemoData : initialDemoData,
        totalDemoLength : initialDemoData.length,
        currentBlogData : currentBlogData,
        searchBlogData  : searchBlogData,
        initialBlogData : initialBlogData,
        totalBlogLength : initialBlogData.length
    },
    mutations:{
        search(state,key){
            state.currentBlogData = [];
            state.searchBlogData = [];
            state.initialBlogData.forEach( (e,i) => { 
               if( formatSearch(e.brief).includes(key) || formatSearch(e.title).includes(key) ){
                   state.searchBlogData.push(state.initialBlogData[i]);
                   if( state.currentBlogData.length < state.pageSize ){
                        state.currentBlogData.push(state.initialBlogData[i]);
                   }
               }
            });
            document.documentElement.scrollTop = document.body.scrollTop = 0;
            state.totalBlogLength = state.searchBlogData.length;
        },
        searchForType(state,name){
            const that = this;
            let arr = [];
            state.searchBlogData.forEach( (element,index) => {
                element.tags.forEach( (e,i) => {
                    if( name === e.name ){
                        arr.push(element);
                    }
                })
            });
            state.currentBlogData = arr;
            state.totalBlogLength = arr.length;
        },
        pageChange(state,obj){
            // 
            const getData = (initial) =>{
                const start = ( obj.currentPage - 1 ) * state.pageSize - 1;
                const end = obj.currentPage * state.pageSize - 1;
                let current = [];
                initial.forEach( (e,i) => {
                    if( i > start && i <= end ){
                        current.push(initial[i]);
                    }
                });
                return current;
            };
            //  1 demo , 0 blog
            if( obj.type === 1 ){
                state.currentDemoData = getData(state.searchDemoData);
                state.totalDemoLength = state.searchDemoData.length;
            }else if( obj.type === 0 ){
                state.currentBlogData = getData(state.searchBlogData);
                state.totalBlogLength = state.searchBlogData.length;
            }
            document.documentElement.scrollTop = document.body.scrollTop = 0;
        }
    }
});
new Vue({
    router,
    store,
    el: "#app",
    render: h => h(App)
});



