<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle"></p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle"></p>
            <p>
                 <span class="code">a</span>  <span class="code">div</span>  <span class="code">preventDefault()</span> 
            </p>
            <a target="_blank" href="/#/home">
                <div class="edit">
                    <el-button type="primary" size="small" @click="count_21++"></el-button> 
                    <p>{{count_21}}</p>               
                </div>
            </a>
             <span class="code">button</span> <span class="code">button</span>  <span class="code">a</span> 
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">a</span><span class="attr"> class=</span><span class="str">"/home"</span><span class="attr"> target=</span><span class="str">"_blank"</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">div</span><span class="attr"> class=</span><span class="str">"edit"</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">button</span><span class="attr"> onclick=</span><span class="str">"myClick"</span><span class="arrow">></span><span class="word"></span><span class="arrow">&lt;/</span><span class="tag">button</span><span class="arrow">></span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;/</span><span class="tag">div</span><span class="arrow">></span><br><span class="arrow">&lt;/</span><span class="tag">a</span><span class="arrow">></span><br><span class="arrow">&lt;</span><span class="tag">style</span><span class="arrow">></span><br><span class="key">.edit</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">height</span><span class="word">:</span><span class="str">100px</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">border</span><span class="word">:</span><span class="str">1px solid </span><span class="key">#eee</span><span class="word">;</span><br><span class="flower">}</span><br><span class="arrow">&lt;/</span><span class="tag">style</span><span class="arrow">></span></pre>
            <p>
                 <span class="code">e.preventDefault()</span>  <span class="code">a</span> <span class="code">e</span> 
            </p>
            <a target="_blank" href="/#/home">
                <div class="edit">
                    <el-button type="primary" size="small" @click.prevent="count_21++"></el-button>
                    <p>{{count_21}}</p>        
                </div>
            </a>
            <p> <span class="code">vue</span> <span class="code">@click.prevent="count++"</span><span class="code">e.preventDefault()</span>  js </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>
<style>
    .edit{
        height:100px;
        border:1px solid #eee;
    }
    .edit .el-button{
        margin:15px;
    }
</style>


