<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle"></p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle"></p>
            <p>
                 <span class="code">textarea</span>  <span class="code">textarea</span> 
            </p>
            <pre class="code"><span class="com">/* 1 */</span><br><span class="key">textarea</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">resize</span><span class="word">:</span><span class="key">none</span><span class="word">;</span><br><span class="flower">}</span><br><span class="com">/* 2 */</span><br><span class="key">textarea</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">max-width</span><span class="word">:</span><span class="key">100%</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">width</span><span class="word">:</span><span class="key">1500px</span><span class="word">;</span><span class="com">/*  */</span><br><span class="flower">}</span><br></pre>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


