            <p class="minititle">2</p>
            <pre class="code"><span class="key">let </span><span class="param">res</span><span class="key"> = </span><span class="flower">{ </span><span class="spe">a</span><span class="word">:1, </span><span class="spe">b</span><span class="word">:2</span><span class="flower"> }</span><span class="word">;</span><br><span class="com">//  resres  {a:1,b:2,c:3,d:4}</span><br><span class="spe">$</span><span class="word">.</span><span class="method">extend</span><span class="word">(</span><span class="param">res</span><span class="word">,</span><span class="flower">{</span><span class="spe">c</span><span class="word">:3</span><span class="flower">}</span><span class="word">,</span><span class="flower">{</span><span class="spe">d</span><span class="word">:4</span><span class="flower">}</span><span class="word">);</span><br><span class="com">// </span><br><span class="key">let </span><span class="param">Res</span><span class="key"> = </span><span class="spe">$</span><span class="word">.</span><span class="method">extend</span><span class="word">(</span><span class="flower">{}</span><span class="word">,</span><span class="param">res</span><span class="word">,</span><span class="flower">{</span><span class="spe">c</span><span class="word">:3</span><span class="flower">}</span><span class="word">,</span><span class="flower">{</span><span class="spe">d</span><span class="word">:4</span><span class="flower">}</span><span class="word">);</span></pre>
            <p class="minititle">3</p>
