/span><span class="param">res2</span><span class="key"> = </span><span class="spe">$</span><span class="word">.</span><span class="method">extend</span><span class="word">(</span><span class="key">false</span><span class="word">,</span><span class="param">obj1</span><span class="word">,</span><span class="param">obj2</span><span class="word">);</span><br><span class="com">/*  </span><br><span class="com">res1 = {</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">name:'John',</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">age:23,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">gender:'male',</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">address:{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">province:'',</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">city:''</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">}</span><br><span class="com">}</span><br><span class="com">res2 = {</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">name:'John',</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">age:23,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">gender:'male',</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">address:{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">city:''</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">}</span><br><span class="com">}</span><br><span class="com">*/</span></pre>
            <p class="minititle">5$.fn.extend()</p>
