            </p>
        </header>
        <section>
            <p class="subtitle"></p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle"></p>
            <p>
                 <span class="tip">IE</span>  <span class="tip">babel-polyfill </span>
            </p>
            <p>
                
            </p>
            <pre class="cmd">npm install babel-polyfill</pre>
            <p>
                 <span class="tip">main.js</span> 
            </p>
            <pre class="code"><span class="key">import </span><span class="str">'babel-polyfill'</span></pre>
            <p>
                 <span class="tip">axios</span>  <span class="tip">IE9</span> <span class="tip">axios</span>  <span class="tip">IE9</span> <span class="tip">IE10</span>
            </p>
            <p>
                 <span class="tip">IE9</span> 
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


