<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle"></p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle"></p>
            <p>
                 <span class="code">&lt;input&nbsp;type="password"/&gt;</span> 
            </p>
            <p>
                
            </p>
            <p>
                 <span class="code">autocomplete="off"</span>  <span class="tip">Chrome</span>  <span class="tip">Chrome</span><span class="tip">FireFox</span>  <span class="tip">IE</span>  <span class="tip">IE</span> 
            </p>
            <p>
                 <span class="code">autocomplete</span>  <span class="code">on</span>  <span class="code">off</span>  <span class="tip">W3School</span>  <span class="code">new-password</span>
            </p>
            <p>
                
            </p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">input</span><span class="attr"> auto-complete=</span><span class="str">"new-password"</span><span class="attr"> type=</span><span class="str">"password"</span><span class="arrow">/&gt;</span></pre>
            <p>
                <span class="code">autocomplete</span>  <span class="code">auto-complete</span>  <span class="tip">Chrome</span> 
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


