<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle"></p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle"></p>
            <p>
                <span class="tip">$http</span>  <span class="tip">get</span>  <span class="tip">axios</span> 
            </p>
            <pre class="code"><span class="com">/*  */</span><br><span class="method">$http</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">url</span><span class="word">:</span><span class="str">''</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">method</span><span class="word">:</span><span class="str">'get'</span><span class="word">,</span><span class="com"> // post</span><br><span class="flower">}</span><span class="word">);</span><br><span class="com">/*  */</span><br><span class="method">$http</span><span class="word">.</span><span class="method">get</span><span class="word">(</span><span class="str">''</span><span class="word">,</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">params</span><span class="word">:</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">a</span><span class="word">:</span><span class="str">'a1'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">b</span><span class="word">:</span><span class="str">'key'</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span><span class="word">).</span><span class="method">success</span><span class="word">(</span><span class="key">function</span><span class="word">(</span><span class="word">data, status, config, headers)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// </span><br><span class="flower">}</span><span class="word">).</span><span class="method">error</span><span class="word">(</span><span class="key">function</span><span class="word">(</span><span class="word">data)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// </span><br><span class="flower">}</span><span class="word">);</span></pre>
            <p>
                <span class="tip">post</span>  <span class="code">$http</span> 
            </p>
            <p>
                
            </p>
            <p class="error">
                $http( ... ).success is not a function
            </p>
            <p>
                 <span class="tip">Angular</span>  <span class="code">then</span>  <span class="code">catch</span> <span class="code">success</span>  <span class="code">error</span>
            </p>
            <p>
                
            </p>
            <pre class="code"><span class="com">// promise </span><br><span class="param">promise</span><span class="word">.</span><span class="method">then</span><span class="word">(</span><span class="key">function</span><span class="word">( res )</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">( res );</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">/* res </span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">data:Object,status:200,config:Object,statusText:'OK' */</span><br><span class="flower">}</span></pre>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


