                <a href="http://www.iana.org/assignments/media-types/media-types.xhtml" target="_blank">http://www.iana.org/assignments/media-types/media-types.xhtml</a>
            </p>
            <p>
                 <span class="tip">IE9</span>  <span class="tip">js</span> 
            </p>
            <p>
                <span class="tip">W3School</span> 
            </p>
            <p>
                
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


