            </p>
        </header>
        <section>
            <p class="subtitle"></p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle"></p>
            <p> <span class="code">placeholder</span>  <span class="code">color</span> </p>
            <p></p>
            <pre class="code"><br><span class="com"> /* WebKit browsers */</span><br><span class="key">input</span><span class="word">::</span><span class="key">-webkit-input-placeholder</span> <span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">color</span><span class="word">:</span><span class="key">#fff</span><span class="word">;</span><br><span class="flower">}</span><br><span class="com"> /* Mozilla Firefox 4 to 18 */</span><br><span class="key">input</span><span class="word">:</span><span class="key">-moz-placeholder</span> <span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">color</span><span class="word">:</span><span class="key">#fff</span><span class="word">;</span><br><span class="flower">}</span><br><span class="com"> /* Mozilla Firefox 19+ */</span><br><span class="key">input</span><span class="word">::</span><span class="key">-moz-placeholder</span> <span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">color</span><span class="word">:</span><span class="key">#fff</span><span class="word">;</span><br><span class="flower">}</span><br><span class="com"> /* Internet Explorer 10+ */</span><br><span class="key">input</span><span class="word">:</span><span class="key">-ms-input-placeholder</span> <span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">color</span><span class="word">:</span><span class="key">#fff</span><span class="word">;</span><br><span class="flower">}</span><br></pre>
        </section>
        <p></p>
        <p></p>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>
<style>
    input::-webkit-input-placeholder { /* WebKit browsers */
        color: #fff;
    }
    input:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
        color: #fff;
    }
    input::-moz-placeholder { /* Mozilla Firefox 19+ */
        color: #fff;
    }
    input:-ms-input-placeholder { /* Internet Explorer 10+ */
        color: #fff;
    }
</style>


