<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle"></p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle"></p>
            <p class="minititle"></p>
            <p>
                 <span class="tip">node.js</span> <a target="_blank" href="https://nodejs.org/en/download/">nodejs.org/en/download/</a> <span class="tip">node.js</span>  <span class="tip">npm</span> 
            </p>
            <p class="minititle"></p>
            <p>
                 <span class="tip">cmd</span>  <span class="code">node -v</span>  <span class="tip">node.js</span> 
            </p>
           <p>
               
           </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


