<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle"></p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle"></p>
            <p>
                <span class="code">this</span>   <span class="code">event.target</span>  <span class="code">click</span><span class="code">keyup</span><span class="code">hover</span> 
            </p>
            <p>
               <span class="code">event.target</span>  <span class="tip">DOM</span>  <span class="code">this</span>   <span class="code">event.target</span>  <span class="code">this</span>  <span class="tip">DOM</span>  <span class="code">event.target</span> 
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


