<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle"></p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle"></p>
            <p>
               <span class="tip">echarts</span> <a target="_blank" href="http://echarts.baidu.com/">http://echarts.baidu.com/</a>
            </p>
            <p>
                 <span class="tip">echarts@3.6.2</span> 
            </p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">div </span><span class="attr">id=</span><span class="str">"pieChart"</span><span class="arrow">></span><span class="arrow">&lt;/</span><span class="tag">div</span><span class="arrow">></span><br><span class="arrow">&lt;</span><span class="tag">script</span><span class="arrow">></span><br><span class="key">var </span><span class="param">pieChart</span><span class="key"> = </span><span class="spe">document</span><span class="word">.</span><span class="method">getElementById</span><span class="word">(</span><span class="str">'pieChart'</span><span class="word">);</span><br><span class="param">pieChart</span><span class="word">.</span><span class="fun">showLoading</span><span class="word">();</span><br><span class="param">pieChart</span><span class="word">.</span><span class="fun">setOption</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">title</span><span class="str">:</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">text</span><span class="str">:</span><span class="str">'2016'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">subtext</span><span class="str">:</span><span class="str">''</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">x</span><span class="str">:</span><span class="str">'center'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">tooltip</span><span class="str">:</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">trigger</span><span class="str">:</span><span class="str">'item'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">format</span><span class="str">:</span><span class="str">'</span><span class="word">{a}&lt;</span><span class="tag">br</span><span class="word">/>{b}:{c}({d}%)</span><span class="str">:</span><span class="str">'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">x</span><span class="str">:</span><span class="str">'center'</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">legend</span><span class="str">:</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">orient</span><span class="str">:</span><span class="str">'vertical'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">left</span><span class="str">:</span><span class="str">'left'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">data</span><span class="str">:</span><span class="rect">[</span><span class="str">''</span><span class="word">,</span><span class="str">''</span><span class="word">,</span><span class="str">''</span><span class="word">,</span><span class="str">''</span><span class="rect">]</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">series</span><span class="str">:</span><span class="rect">[</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">name</span><span class="str">:</span><span class="str">''</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">type</span><span class="str">:</span><span class="str">'pie'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">radius</span><span class="str">:</span><span class="str">'55%'</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">center</span><span class="str">:</span><span class="rect">[</span><span class="str">'50%'</span><span class="word">,</span><span class="str">'60%'</span><span class="rect">]</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">data</span><span class="str">:</span><span class="rect">[</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">{</span><span class="spe">value</span><span class="str">:</span><span class="word">2165</span><span class="word">,</span><span class="spe">name</span><span class="str">:''</span><span class="flower">}</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">{</span><span class="spe">value</span><span class="str">:</span><span class="word">3892</span><span class="word">,</span><span class="spe">name</span><span class="str">:''</span><span class="flower">}</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">{</span><span class="spe">value</span><span class="str">:</span><span class="word">4120</span><span class="word">,</span><span class="spe">name</span><span class="str">:''</span><span class="flower">}</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">{</span><span class="spe">value</span><span class="str">:</span><span class="word">1246</span><span class="word">,</span><span class="spe">name</span><span class="str">:''</span><span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="rect">]</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="rect">]</span><br><span class="flower">}</span><span class="word">);</span><br><span class="param">pieChart</span><span class="word">.</span><span class="fun">hideLoading</span><span class="word">();</span><br><span class="arrow">&lt;/</span><span class="tag">script</span><span class="arrow">></span></pre>
            <p>
             <span class="tip">echarts</span>
            </p>
            <p>
             <span class="tip">vue</span>  <span class="tip">v-charts</span>  <span class="tip">echarts</span>  <span class="tip">echarts</span> <span class="code">methods</span> <span class="code">drawPie(id)</span> <span class="code">mounted</span>  <span class="code">this.drawPie('pieChart')</span>
            </p>
            <p></p>
            <div id="pieChart"></div>
        </section>
    </div>
</template>
<script>
    import echarts from 'echarts'
    export default{
        data(){
             const arr = this.$store.state.initialBlogData;
        return {
            thisBlog: arr[ arr.length - 1 - this.order() ],
                chart: null,
                head: ['','','',''],
                headData: [ 
                    {value:2165,name:''},
                    {value:3892,name:''},
                    {value:4120,name:''},
                    {value:1246,name:''}
                ]
            }
        },
        methods:{
            order(){
                return ( window.location.href.substring(window.location.href.indexOf('_')+1) - 1 );
            },
            drawPie(id) {
                // 
                this.chart = echarts.init(document.getElementById(id));
                // 
                this.chart.showLoading();
                // data
                let that = this;
                // set
                this.chart.setOption({
                    title : {
                        text: '2016',
                        subtext: '',
                        x:'center'
                    },
                    tooltip : {
                        trigger: 'item',
                        formatter: "{a} <br/>{b} : {c} ({d}%)"
                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left',
                        data: that.head
                    },
                    series : [
                        {
                            name: '',
                            type: 'pie',
                            radius : '55%',
                            center: ['50%', '60%'],
                            data:that.headData,
                            itemStyle: {
                                emphasis: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
                                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                });
                this.chart.hideLoading()
            }
        },
        mounted(){
            this.drawPie('pieChart');
            document.documentElement.scrollTop = document.body.scrollTop =0
        }
    }
</script>
<style>
    #pieChart{
        width:500px;
        height:500px;
    }
</style>


