            <p>
                <span class="code">history.pushState()</span>  <span class="code">history.replaceState()</span> <span class="tip">history</span> <span class="code">push</span> <span class="code">replace</span> <span class="code">state,title,url</span>  <span class="tip">hash</span>  <span class="tip">#</span> 
