<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle"></p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle"></p>
            <p>
                 <span class="code">hashchange</span>  <span class="tip">url</span> <span class="tip">localhost:1234/index.html => localhost:1234/index.html#hash</span>
            </p>
            <p>
                 <span class="code">window</span>  <span class="code">hashchange</span>  <span class="code">hash</span>  <span class="code">e</span> <span class="code">e.oldURL</span>  <span class="code">e.newURL</span>
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


