<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle"></p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle"></p>
            <p class="minititle"> style </p>
            <pre class="code"><span class="com">//  css </span><br><span class="spe">console</span><span class="word">.</span><span class="method">log</span><span class="word">(</span><span class="spe">document</span><span class="word">.</span><span class="spe">getElementById</span><span class="word">(</span><span class="str">'demo'</span><span class="word">).</span><span class="spe">style</span><span class="word">);</span></pre>
            <p>
                 <span class="tip">style</span> 
            </p>
            <p class="minititle"></p>
             <pre class="code"><span class="key">var </span><span class="param">$demo</span><span class="key"> = </span><span class="spe">document</span><span class="word">.</span><span class="spe">getElementById</span><span class="word">(</span><span class="str">'demo'</span><span class="word">);</span><br><span class="com">// </span><br><span class="spe">console</span><span class="word">.</span><span class="method">log</span><span class="word">(</span><span class="spe">window</span><span class="word">.</span><span class="spe">getComputedStyle</span><span class="word">(</span><span class="param">$demo</span><span class="word">));</span></pre>
             <p> <span class="tip">jQuery</span> </p>
             <pre class="code"><span class="spe">console</span><span class="word">.</span><span class="method">log</span><span class="word">(</span><span class="spe">$</span><span class="word">(</span><span class="str">'#demo'</span><span class="word">).</span><span class="method">css</span><span class="word">(</span><span class="str">'color'</span><span class="word">));</span><span class="com"> //  color</span></pre>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


