<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle"></p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle"></p>
            <p class="minititle"> js </p>
            <p>
                 <span class="code">scrollTop</span> 
            </p>
            <pre class="code"><span class="key">var </span><span class="param">scrollTop </span><span class="key">= </span><span class="spe">window</span><span class="word">.</span><span class="spe">pageYOffset</span><span class="key">||</span><span class="spe">document</span><span class="word">.</span><span class="spe">documentElement</span><span class="word">.</span><span class="spe">scrollTop</span><span class="key">||</span><span class="spe">document</span><span class="word">.</span><span class="spe">body</span><span class="word">.</span><span class="spe">scrollTop</span><span class="word">;</span></pre>
            <p>
                <span class="code">html</span>  <span class="code">doctype</span>  <span class="code">scrollTop</span> 
            </p>
            <p class="minititle">jQuery </p>
            <p>
                 <span class="tip">DOM</span>  <span class="code">scrollTop()</span> 
            </p>
            <pre class="code"><span class="spe">$</span><span class="word">(</span><span class="str">'body'</span><span class="word">).</span><span class="method">scrollTop()</span><span class="word">;</span></pre>
            <p>
                <span class="tip">jQuery</span>  <span class="code">scrollTop()</span> <span class="tip">0</span>
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


